.episodeBlock {

  vertical-align: top;
  margin-bottom: 40px;

  &.activeVideo {
    img {
      border: 4px solid $accent-color;
    }
  }

  .thumb-wrapper {
    position: relative;

    &:hover {

      button {
        opacity: 1;
      }
    }

    button {
      display: none;
      position: absolute;
      z-index: 20;
      top: 50%;
      left: 50%;
      @include css3-prefix(transform, translateX(-50%) translateY(-50%) scale(0.66, 0.66));
      opacity: 0.5;
      @include css3-prefix(transition-duration, 0.6s);

    }

    img {
      position: relative;
      width: 100%;
      @include css3-prefix(transition, 0.5s opacity);
      margin-bottom: 10px;
      display: block;
    }

  }

  .episodeBlockContent {
    font-family: "Guardian Egyptian Web", "Egyptian Display", "Guardian Text Egyptian Web", "Egyptian Text", Georgia, serif;
  }

  &.inactiveVideo {
    cursor: pointer;

    .thumb-wrapper {

      button {
        display: block;
      }
    }

    &:hover {
      img {
        opacity: 0.8;
      }
    }
  }

  @media(min-width: 440px) {
    //    width: 90%;
    display: block;
    //    margin: 0 auto 20px;
  }

  @media(min-width: 480px) {
    p {
      font-size: 16px;
    }
  }

  @media(min-width: 980px) {
    margin-right: 2%;
    padding: 0;
    width: 40%;
    display: inline-block;

    &:nth-of-type(4n) {
      margin-right: 0;
    }
    &:nth-of-type(2n) {
      padding: 0;
    }

  }

  @media(min-width: 1140px) {
    p {
      font-size: 18px;
      span {
        font-size: 14px;
      }
    }
  }

}